import React from "react";
import { motion } from "framer-motion";

export const transitionRight = (AnimComponent) => {

    return (props) => (<>
        <motion.div
            initial={{ x: -2000 }}
            animate={{ x: 0 }}
            exit={{ x: -2000 }}
            transition={{ duration: 1.3, ease: [0.22, 1, 0.36, 1] }}
            style={{
                overflowX: "hidden"
            }}
        >
            <AnimComponent {...props} />
        </motion.div>
    </>)
}
