import React, { useEffect, useState } from "react";
import { Navigate } from "react-router-dom";

import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';
import ThreeSixtyIcon from '@mui/icons-material/ThreeSixty';
import AlarmIcon from '@mui/icons-material/Alarm';
import { Button } from "@mui/material";
import SvgIcon from '@mui/material/SvgIcon';
import { transitionLeft } from "./transitions";
import { motion } from "framer-motion";

function SvgIconChildren() {
    return (
        <SvgIcon>
            {/* credit: plus icon from https://heroicons.com/ */}
            <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
            >
                <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M4.5 12a7.5 7.5 0 0015 0m-15 0a7.5 7.5 0 1115 0m-15 0H3m16.5 0H21m-1.5 0H12m-8.457 3.077l1.41-.513m14.095-5.13l1.41-.513M5.106 17.785l1.15-.964m11.49-9.642l1.149-.964M7.501 19.795l.75-1.3m7.5-12.99l.75-1.3m-6.063 16.658l.26-1.477m2.605-14.772l.26-1.477m0 17.726l-.26-1.477M10.698 4.614l-.26-1.477M16.5 19.794l-.75-1.299M7.5 4.205L12 12m6.894 5.785l-1.149-.964M6.256 7.178l-1.15-.964m15.352 8.864l-1.41-.513M4.954 9.435l-1.41-.514M12.002 12l-3.75 6.495"
                />
            </svg>
        </SvgIcon>
    );
}
const PlayerTab = ({ timeWiner, allGames, user, setTimeWiner, setUser, setUserMenu, userMenu, symbolPerSecond }) => {

    const [restart, setRestart] = useState(false);
    const [sortAscending, setSortAscending] = useState(false);
    const [sortNamesAscending, setSortNamesAscending] = useState(false);
    const [swapPage, setSwapPage] = useState(true)
    const [swapTop, setSwapTop] = useState(false)
    const sortAllGames = (sortAscending, sortNamesAscending) => {
        let sortByField;
        if (sortNamesAscending) {
            sortByField = "name"
        } else {
            sortByField = "time"
        }
        const nameTimePairs = allGames.names.map((name, index) => ({
            name,
            time: allGames.times[index],
            sps: allGames.sps[index],
        }));

        nameTimePairs.sort((a, b) => {
            if (sortByField === "time") {
                const timeA = timeStringToMilliseconds(a.time);
                const timeB = timeStringToMilliseconds(b.time);

                if (sortAscending) {
                    return timeA - timeB;
                } else {
                    return timeB - timeA;
                }
            } else if (sortByField === "name") {
                if (sortAscending) {
                    return a.name.localeCompare(b.name);
                } else {
                    return b.name.localeCompare(a.name);
                }
            }
            return 0;
        });

        const sortedNames = nameTimePairs.map(pair => pair.name);
        const sortedTimes = nameTimePairs.map(pair => pair.time);
        const sortedSps = nameTimePairs.map(pair => pair.sps);

        return { names: sortedNames, times: sortedTimes, sps: sortedSps };
    };
    function getBestResultsByName() {
        const nameTimePairs = allGames.names.map((name, index) => ({
            name,
            time: allGames.times[index],
            sps: allGames.sps[index],
        }));

        //  ,      
        const bestResultsByName = {};
        nameTimePairs.forEach(pair => {
            if (!bestResultsByName[pair.name]) {
                bestResultsByName[pair.name] = pair;
            } else {
                const timeA = timeStringToMilliseconds(bestResultsByName[pair.name].time);
                const timeB = timeStringToMilliseconds(pair.time);
                //     
                if (timeB < timeA) {
                    bestResultsByName[pair.name] = pair;
                }
                //     sps
                if (pair.sps > bestResultsByName[pair.name].sps) {
                    bestResultsByName[pair.name].sps = pair.sps;
                }
            }
        });

        //     
        const bestResultsArray = Object.values(bestResultsByName);

        return bestResultsArray;
    }

    const timeStringToMilliseconds = (time) => {
        const [hours, minutes, seconds] = time.split(":").map(Number);
        return hours * 3600000 + minutes * 60000 + seconds * 1000;
    };

    const bestResults = getBestResultsByName();
    useEffect(() => {
        if (!swapPage) {
            setTimeout(() => {
                setRestart(true)
            }, 100);
        } else if (swapTop) {
            setTimeout(() => {
                setSortAscending(false)
                setSortNamesAscending(false)
                setUser({ name: "" })
                setTimeWiner(null)
                setSortAscending(false)
            }, 100);
        }
    }, [swapPage, swapTop, setUser, setTimeWiner])
    return (<motion.div
        initial={!swapPage ? { x: 0 } : (swapTop && { y: 0, opacity: 1 })}
        animate={!swapPage ? { x: 2000 } : (swapTop && { y: 1800, opacity: 0 })}
        exit={!swapPage ? { x: 2000 } : (swapTop && { y: 1800, opacity: 0 })}
        transition={{ duration: 1.3, ease: [0.22, 1, 0.36, 1] }}
        style={{ overflowX: "hidden" }}
    >
        {user.name === "" && <Navigate to="/login" />}
        {restart && <Navigate to="/game" />}
        <div>
            <div onClick={() => setUserMenu(false)} className={userMenu ? "user-menu__bg active" : "user-menu__bg"}>
                <div className={userMenu ? "user-menu active" : "user-menu"}>
                    {Array.isArray(allGames.names) && bestResults.map((result, index) => (
                        <Button onClick={() => {
                            setUser({ name: result.name })
                            setUserMenu(false)
                        }} color="info" style={{ textTransform: "initial" }} key={index}>{result.name}: {result.time}, {result.sps}SPS</Button>
                    ))}
                </div>
            </div>
            <div className="tab-box">
                <div>
                    <div>
                        <button onClick={() => setUserMenu(!userMenu)} className="change-name">
                            <span className="change-name__name">{user.name}</span>
                            <SvgIconChildren className="change-name__img" /></button>
                    </div>
                    <div>
                        {timeWiner !== null ? <div>
                            <h3 >Last attempt: {timeWiner}</h3>
                            <h3>
                                SPS: {symbolPerSecond}
                            </h3>
                        </div> : <h3>Try again</h3>}
                        <Button style={{ marginRight: "10px" }} variant="contained" color="success" size="small"
                            onClick={() => {
                                setSwapTop(true)
                            }}>Add user
                        </Button>
                        <Button
                            onClick={() => {
                                setSortAscending(false)
                                setSortNamesAscending(false)
                                setSwapPage(false)
                                setTimeWiner(null)
                                setSortAscending(false)
                            }}
                            variant="outlined" size="small">
                            Restart
                            <ThreeSixtyIcon />
                        </Button>
                    </div>
                </div>
                <TableContainer component={Paper} style={{ minWidth: "300px" }}>
                    <Table aria-label="simple table" >
                        <TableHead>
                            <TableRow>
                                <TableCell className={sortNamesAscending ? "time-box active" : "time-box"}
                                    onClick={() => {
                                        setSortAscending(!sortAscending)
                                        setSortNamesAscending(true)
                                    }}>Name</TableCell>
                                <TableCell className={!sortNamesAscending ? "time-box active" : "time-box"} align="right"
                                    onClick={() => {
                                        setSortAscending(!sortAscending)
                                        setSortNamesAscending(false)
                                    }}>
                                    Time<AlarmIcon className="time-box__icon" />
                                </TableCell>
                                <TableCell align="right">SPS</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {Array.isArray(allGames.names) && sortAllGames(sortAscending, sortNamesAscending).names.map((name, index) => (
                                <TableRow
                                    key={index}
                                    sx={{ '&:last-child td, &:last-child th': { border: 0 } }}
                                >
                                    <TableCell component="th" scope="row">
                                        {name}
                                    </TableCell>
                                    <TableCell align="right">{sortAllGames(sortAscending, sortNamesAscending).times[index]}</TableCell>
                                    <TableCell align="right">{sortAllGames(sortAscending, sortNamesAscending).sps[index]}</TableCell>
                                </TableRow>
                            ))}
                        </TableBody>

                    </Table>
                </TableContainer>
            </div>
        </div>
    </motion.div >
    );
};
export default transitionLeft(PlayerTab)