import React, { useState, useRef, useEffect } from "react";
import { Button } from "@mui/material";

export const Seconder = ({ setSeconderStarted, setValueTextArea, gameWon, setGameWon, setTimeWiner, setAllGames, user, animateEnd, setSymbolPerSecond, prevValueTextArea, seconderStarted }) => {
    const [milisecond, setMiliecond] = useState(0);
    const [second, setSecond] = useState(0);
    const [minute, setMinute] = useState(0);
    const intervalRef = useRef(null);

    const secondStart = () => {
        if (intervalRef.current) {
            clearInterval(intervalRef.current);
        }

        intervalRef.current = setInterval(() => {
            setMiliecond(prevMilisecond => {
                if (prevMilisecond >= 99) {
                    setSecond(prevSecond => {
                        if (prevSecond >= 59) {
                            setMinute(prevMinute => prevMinute + 1);
                            return 0;
                        } else {
                            return prevSecond + 1;
                        }
                    });
                    return 0;
                } else {
                    return prevMilisecond + 1;
                }
            });
        }, 10);
        setValueTextArea("")
        setSeconderStarted(true);
        setGameWon(false)
    };
    const resetTimer = () => {
        clearInterval(intervalRef.current); //  
        setMinute(0);
        setSecond(0);
        setMiliecond(0);
        setValueTextArea(prevValueTextArea)
        setSeconderStarted(false); //    
    };
    useEffect(() => {
        if (gameWon) {
            let time = `${minute.toString().padStart(2, '0')}:${second.toString().padStart(2, '0')}:${milisecond.toString().padStart(2, '0')}`
            let name = user.name
            setTimeWiner(time)
            const timeWinerInMilliseconds = (minute * 60 + second) * 1000 + milisecond;
            const nonSpaceChars = prevValueTextArea.replace(/\s/g, ''); //   
            const charactersPerSecond = nonSpaceChars.length / (timeWinerInMilliseconds / 1000);
            let sps;
            if (second < 1) {
                const formattedCharactersPerSecond = charactersPerSecond.toFixed(1);
                sps = `0.${formattedCharactersPerSecond.replace(/\./g, '')}`;
                sps = sps.substring(0, 4);
            } else {
                sps = charactersPerSecond.toFixed(2)
            }
            setSymbolPerSecond(sps)
            setAllGames((prevAllGames) => {
                return {
                    ...prevAllGames,
                    names: prevAllGames.names ? [...prevAllGames.names, name] : [name],
                    times: prevAllGames.times ? [...prevAllGames.times, time] : [time],
                    sps: prevAllGames.sps ? [...prevAllGames.sps, sps] : [sps],
                };
            })
            clearInterval(intervalRef.current);
        } else if (animateEnd) {
            clearInterval(intervalRef.current);
        }
    }, [gameWon, minute, second, milisecond, setTimeWiner, setAllGames, user, animateEnd, prevValueTextArea, setSymbolPerSecond]);
    useEffect(() => {
        return () => {
            //  ,   
            if (intervalRef.current) {
                clearInterval(intervalRef.current);
            }
        };
    }, [])
    return (
        <div className="secondomer">
            <div>{`${minute.toString().padStart(2, '0')}:${second.toString().padStart(2, '0')}:${milisecond.toString().padStart(2, '0')}`}</div>

            {seconderStarted ? <Button size="small" color="error" style={{ pointerEvents: "all", opacity: "1" }} onClick={resetTimer} variant="contained">
                Reset
            </Button> : <Button className={seconderStarted || animateEnd ? "pointNone" : ""} size="small" onClick={secondStart} variant="contained">
                Start
            </Button>}
        </div>
    );
};
