import React, { useState } from "react";
import Texter from "./Texter";
import PlayerTab from "./PlayerTab";
import {
    // BrowserRouter,
    HashRouter,
    Navigate,
    Route, Routes
} from "react-router-dom";
import LocalLogin from "./LocalLogin";
export const MainComponent = () => {
    const [allGames, setAllGames] = useState({ names: [], times: [], sps: [] });
    const [timeWiner, setTimeWiner] = useState(null)
    const [user, setUser] = useState({ name: "", color: "" })
    const [userNames, setUserNames] = useState({ names: [] })
    const [userMenu, setUserMenu] = useState(false)
    const [symbolPerSecond, setSymbolPerSecond] = useState(0)
    const uniqueNames = [...new Set(userNames.names)];
    const uniqueUsers = [...new Set(allGames.names)];

    return (<div style={{ zIndex: "1" }}>
        {/* <BrowserRouter basename={process.env.PUBLIC_URL}> */}
        <HashRouter>
            <Routes>
                <Route path="/" element={<Navigate to="/login" />} />
                <Route path="/login" element={<LocalLogin user={user} setUser={setUser} setUserNames={setUserNames} allGames={allGames} uniqueNamesAll={uniqueNames} />} />
                <Route path="/game" element={<Texter allGames={allGames}
                    setAllGames={setAllGames} setTimeWiner={setTimeWiner}
                    user={user} setUser={setUser} timeWiner={timeWiner}
                    setUserMenu={setUserMenu} userMenu={userMenu} uniqueUsers={uniqueNames}
                    setSymbolPerSecond={setSymbolPerSecond}
                />} />
                <Route path="/tab" element={<PlayerTab user={user} timeWiner={timeWiner}
                    allGames={allGames} setTimeWiner={setTimeWiner}
                    setUser={setUser} setUserMenu={setUserMenu} userMenu={userMenu} uniqueUsers={uniqueUsers}
                    symbolPerSecond={symbolPerSecond}
                />} />
            </Routes>
        </HashRouter>
    </div>)
}