import React, { useEffect, useState, useRef } from "react";
import { Seconder } from "./Seconder";
import "../css/style.css"
import { Navigate } from "react-router-dom";
import TextField from '@mui/material/TextField';
import { Button } from "@mui/material";
import SvgIcon from '@mui/material/SvgIcon';
import { motion } from "framer-motion";
import { transitionRight } from "./transitions";
function SvgIconChildren() {
    return (
        <SvgIcon>
            {/* credit: plus icon from https://heroicons.com/ */}
            <svg
                xmlns="http://www.w3.org/2000/svg"
                fill="none"
                viewBox="0 0 24 24"
                strokeWidth={1.5}
                stroke="currentColor"
            >
                <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    d="M4.5 12a7.5 7.5 0 0015 0m-15 0a7.5 7.5 0 1115 0m-15 0H3m16.5 0H21m-1.5 0H12m-8.457 3.077l1.41-.513m14.095-5.13l1.41-.513M5.106 17.785l1.15-.964m11.49-9.642l1.149-.964M7.501 19.795l.75-1.3m7.5-12.99l.75-1.3m-6.063 16.658l.26-1.477m2.605-14.772l.26-1.477m0 17.726l-.26-1.477M10.698 4.614l-.26-1.477M16.5 19.794l-.75-1.299M7.5 4.205L12 12m6.894 5.785l-1.149-.964M6.256 7.178l-1.15-.964m15.352 8.864l-1.41-.513M4.954 9.435l-1.41-.514M12.002 12l-3.75 6.495"
                />
            </svg>
        </SvgIcon>
    );
}
let startInitialValue = (name) => {
    return (`${name}`)
}
let lvl1 = (name) => {
    return (
        `#Hello ${name}
##The point of the game is to write this text along with HTML tags as fast as possible.
###When you're done, you'll see yourself in the ranking table.
<input/>
<button>click</button>`)
}
let lvl2 = () => {
    return (`<h1>This is heading 1</h1>
<h2>This is heading 2</h2>
<h3>This is heading 3</h3>`)
}
let lvl3 = () => {
    return (`<h1>   </h1>
<p> .       . !</p>
<h2> 1</h2>
<h3> 1</h3>
<p>  </p>
<p>  </p>
<h3> 2</h3>
<p>  </p>
<p>  </p>
<h4></h4>
<p>     </p>
<h3> 3</h3>
<p>  </p>
<p>  </p>
<h2> 2</h2>`)
}
const levels = [lvl1, lvl2, lvl3, "lvl4", "lvl5"];

const Texter = (props) => {
    const { allGames, setAllGames, timeWiner, setTimeWiner, user, setUserMenu, userMenu, uniqueUsers, setUser, setSymbolPerSecond } = props;
    const [valueTextArea, setValueTextArea] = useState("")
    const [prevValueTextArea, setPrevValueTextArea] = useState()
    const [seconderStarted, setSeconderStarted] = useState(false)
    const [gameWon, setGameWon] = useState(false)
    const [activeLvl, setActiveLvl] = useState(0)
    const [animateEnd, setAnimateEnd] = useState(false)
    const [swapPage, setSwapPage] = useState(true)
    const [goToTab, setGoToTab] = useState(true)

    const textFieldRef = useRef(null);
    const handleLevelClick = (level) => {
        setActiveLvl(level);
        setValueTextArea(levels[level - 1]);
        setPrevValueTextArea(levels[level - 1]);
    };
    const onChangeTextArea = (e) => {
        if (seconderStarted) {
            setValueTextArea(e.target.value)
        } else {
            e.preventDefault()
        }
    }
    useEffect(() => {
        const initialValue = startInitialValue(user.name);
        setPrevValueTextArea(initialValue);
        setValueTextArea(initialValue);
    }, [user.name])
    useEffect(() => {
        if (seconderStarted) {
            textFieldRef.current.focus();
            if (valueTextArea === prevValueTextArea) {
                setSeconderStarted(false);
                setAnimateEnd(true)
                setTimeout(() => {
                    setSwapPage(false)
                    setTimeout(() => {
                        setGameWon(true)
                    }, 100)
                }, 1500)
            }
        }
        if (gameWon) {
        }
    }, [gameWon, seconderStarted, valueTextArea, prevValueTextArea, swapPage]);
    const swapPageOnTab = () => {
        setSwapPage(false)
        setTimeout(() => {
            setGoToTab(false)
        }, 100)
    }
    const processText = (text) => {
        if (text) {
            text = text.replace(/###(.+?)(?=\n|$)/g, "<h3>$1</h3>");
            text = text.replace(/##(.+?)(?=\n|$)/g, "<h2>$1</h2>");
            text = text.replace(/#(.+?)(?=\n|$)/g, "<h1>$1</h1>");
        }
        return text || ""; //   ,  text  
    };

    if (gameWon && timeWiner !== null) {
        return <Navigate to="/tab" />
    } else if (user.name === "") {
        return <Navigate to="/login" />
    } else {
        return (<motion.div
            initial={!swapPage && { x: 0 }}
            animate={!swapPage && { x: -2000 }}
            exit={!swapPage && { x: -2000 }}
            transition={{ duration: 1.3, ease: [0.22, 1, 0.36, 1] }}
            style={{
                maxWidth: "1000px", margin: "0 auto", color: user.color,
                overflow: "hidden"
            }}
        >
            {!goToTab && <Navigate to="/tab" />}
            <div>{animateEnd && <motion.img
                className="slide-out-img"
                initial={{ scale: 1, opacity: 1 }}
                animate={{ scale: 1, opacity: 0 }}
                exit={{ scale: 0, opacity: 0 }}
                transition={{ duration: 1.5, ease: [1.000, 0.030, 1.000, 1.070] }}
                src="https://img1.picmix.com/output/stamp/normal/9/6/7/6/2336769_b9413.gif" alt=""
            />}</div>
            <div className="main-box-game" >
                <div style={{ textAlign: "center", display: "flex", flexDirection: "column" }}>
                    <div className={seconderStarted || animateEnd ? "pointNone box-table" : "box-table"}>
                        <button onClick={() => setUserMenu(!userMenu)} className="change-name">
                            <span className="change-name__name">{user.name}</span>
                            <SvgIconChildren className="change-name__img" /></button>
                        <Button style={{ borderRadius: "10px", }} variant="contained" size="small" onClick={swapPageOnTab}>TAB
                            <img style={{ paddingLeft: "5px", width: "20px" }} src="https://www.svgrepo.com/show/532228/table-list.svg" alt="" /></Button>
                    </div>
                    <div className="secondomer-box">
                        <Seconder setSeconderStarted={setSeconderStarted} timeWiner={timeWiner}
                            setTimeWiner={setTimeWiner} setValueTextArea={setValueTextArea}
                            setGameWon={setGameWon} gameWon={gameWon}
                            allGames={allGames} setAllGames={setAllGames}
                            user={user} animateEnd={animateEnd} setSymbolPerSecond={setSymbolPerSecond}
                            prevValueTextArea={prevValueTextArea} seconderStarted={seconderStarted} />
                    </div>

                </div>
                <TextField
                    id="outlined-multiline-flexible"
                    multiline
                    minRows={5}
                    maxRows={5}
                    onPaste={(e) => { seconderStarted && e.preventDefault() }}
                    style={{ flex: "1 1 auto", minHeight: "110px", color: user.color }}
                    onChange={onChangeTextArea}
                    value={valueTextArea}
                    inputRef={textFieldRef}
                    className={seconderStarted || animateEnd ? "pointNone custom-scrollbar-text" : "custom-scrollbar-text"}
                />
                <div onClick={() => setUserMenu(false)} className={userMenu ? "user-menu__bg active" : "user-menu__bg"}>
                    <div className={userMenu ? "user-menu active" : "user-menu"}>
                        {Array.isArray(allGames.names) && uniqueUsers.map((user, index) => (
                            <Button onClick={() => {
                                setUser({ name: user })
                                setUserMenu(false)
                            }} color="info" style={{ textTransform: "initial" }} key={index}>{user}</Button>
                        ))}
                    </div>
                </div>
                <div className={seconderStarted || animateEnd ? "pointNone custom-scrollbar lvl-content-container" : "custom-scrollbar lvl-content-container"}>
                    <div className={seconderStarted || animateEnd ? "pointNone scroll-content lvl-content" : "scroll-content lvl-content"}>
                        {levels.map((_, index) => (
                            <Button
                                key={index}
                                className={activeLvl === index + 1 ? "" : "lvl-passive"}
                                onClick={() => handleLevelClick(index + 1)}
                            >
                                lvl {index + 1}
                            </Button>
                        ))}
                    </div>
                </div>
            </div>
            <div>
                <CompontentTextArea seconderStarted={seconderStarted} textValues={processText(valueTextArea)} prevTextValues={processText(prevValueTextArea)} />
            </div>
        </motion.div >
        );
    }
}
const CompontentTextArea = ({ textValues, seconderStarted, prevTextValues }) => {
    return (<div className={seconderStarted ? "game-box active" : "game-box"}>
        <div className="custom-scrollbar big">
            <div className="scroll-content big">
                <div className={seconderStarted ? "game-started" : "hiden"}>
                    {prevTextValues.split("\n").map((htmlContent, index) => (
                        <div key={index} style={{ marginBottom: "2px" }} dangerouslySetInnerHTML={{ __html: htmlContent }}></div>
                    ))}
                </div>
                <div className={seconderStarted ? "game-box__text active" : "game-box__text"}>
                    {textValues.split("\n").map((htmlContent, index) => (
                        <div key={index} style={{ marginBottom: "2px" }} dangerouslySetInnerHTML={{ __html: htmlContent }}></div>
                    ))}
                </div>
            </div>
        </div>
    </div >)
}
export default transitionRight(Texter)